import merge from 'ts-deepmerge';

import { SingleVariantIconProps } from '../../types/icon';
import { setDefaultWidthSx } from '../../utils/styleHelpers';
import { Icon } from '../Icon';

export function ImmutableSymbol({ sx = {}, ...props }: SingleVariantIconProps) {
  return (
    <Icon
      {...props}
      icon="Immutable"
      sx={merge(
        { fill: 'base.color.brand.1' },
        setDefaultWidthSx(sx, '32px'),
        sx,
      )}
      variant="bold"
    />
  );
}

ImmutableSymbol.displayName = 'ImmutableSymbol';
