import merge from 'ts-deepmerge';

import { useFetchLogoContent } from '../../hooks/useFetchLogoContent';
import { SingleVariantIconProps } from '../../types/icon';
import { setDefaultWidthSx } from '../../utils/styleHelpers';
import { SvgIcon } from '../SvgIcon';

export function DocsHorizontalLockup({
  sx = {},
  testId,
  ...props
}: SingleVariantIconProps) {
  const svgContent = useFetchLogoContent('DocsHorizontalLockup');
  return (
    <SvgIcon
      {...props}
      viewBox="0 0 275 48"
      testId={testId}
      sx={merge(
        setDefaultWidthSx(sx, '200px'),
        { fill: 'base.color.brand.1' },
        sx,
      )}
    >
      {svgContent && (
        <g
          data-testid={`${testId}__g`}
          dangerouslySetInnerHTML={{ __html: svgContent.join('') }}
        />
      )}
    </SvgIcon>
  );
}
DocsHorizontalLockup.displayName = 'DocsHorizontalLockup';
